<template lang='pug'>
.content
  md-modal(id='modal-view-order', v-model='showModal', @closeModal='closeModalOrder')
    h3(slot='header' class='display-none')
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title CHI TIT HA N
            span M HA N : {{ orderInfor.id }}  |  TNG TIN : {{ orderInfor.totalPrice }} | STATUS : {{ orderInfor.status }}
          .card-content
            form.max-width
              .row#detail-order-header
                .col-md-3
                  md-fg-input(label='H tn', :disabled='true', :labelFloating='false', v-model='fullName')
                .col-md-3
                  md-fg-input(label='Email', :disabled='true', :labelFloating='false', v-model='orderInfor.customer.email')
                .col-md-3
                  md-fg-input(label='Phone', :disabled='true', :labelFloating='false', v-model='orderInfor.customer.phone')
                .col-md-3
                  .image-avatar-order-detail
                    img.img(:src='orderInfor.customer.profilePicUrl', class="image-avatar-order-detail")
              .row#detail-order-body
                .col-md-12
                  <!--p.title Danh sch mn n-->
                  .list-dish-order-wrapper
                    .dish-item(v-for='infor_dish in orderInfor.orderDetails')
                      img.img(:src='infor_dish.dish.imageUrl')
                      .info-dish
                        p.name-dish {{infor_dish.dish.title }}
                        p.common-info - Gi tin : {{infor_dish.dish.price }}
                        p.common-info - S lng : {{infor_dish.quantity }}
                        p.common-info - Tng tin : {{ calcTotalPrice(infor_dish.quantity, infor_dish.dish.price) }}
                        p.common-info - Yu cu thm : {{infor_dish.otherDetail}}

              md-button.btn.pull-right(@click.prevent='closeModalOrder') ng
              md-button.btn.btn-success.pull-right(@click.prevent='confirm' v-if="type=='ordered'") CONFIRM
              md-button.btn.btn-success.pull-right(@click.prevent='delivering' v-if="type=='confirmed'") DELIVERING
              md-button.btn.btn-success.pull-right(@click.prevent='delivered' v-if="type=='delivering'") DELIVERED
              .clearfix







</template>


<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    props: {
      triggerShowModal: {
        type: Boolean,
        default: false
      },
      orderID: {
        type: Number,
        default: 1
      },
      type: {
        type: String,
        default: 'all'
      }

    },
    data() {
      return {
        // showModal: false,
        orderInfor: {
          'id': 0,
          'totalPrice': 0,
          'messengerUserId': '',
          'status': '',
          'customer': {
            'gender': '',
            'firstName': '',
            'lastName': '',
            'profilePicUrl': '',
            'messengerUserId': '',
            'email': '',
            'address': '',
            'phone': '',
            'tier': null
          },
          'orderDetails': [],
          'totalDish': null,
          'request': null,
          'orderTime': null,
          'expectedDeliveryTime': null,
          'deliveryTime': null,
          'rating': null,
          'feedback': null
        }
      }
    },
    computed: {
      showModal() {
        return this.triggerShowModal
      },
      fullName() {
        return this.orderInfor.customer.lastName + ' ' + this.orderInfor.customer.firstName
      }
    },
    watch: {
      triggerShowModal: function (val) {
        var self = this
        if (val) {
          self.getDetailOrder()
        }
      }
    },
    methods: {
      confirm() {
        swal('Thnh cng!', ' chnh sa thnh cng!', 'success')

        // var self = this
        // axios.post('/users/update-profile', {
        //   'address': self.user.address,
        //   'email': self.user.email,
        //   'phoneNumber': self.user.phoneNumber,
        //   'userName': self.user.userName
        // }, {
        //   headers: {Authorization: self.$store.state.userToken}
        // })
        //   .then(function (response) {
        //     swal('Thnh cng!', ' chnh sa thnh cng!', 'success')
        //   })
      },
      closeModalOrder() {
        this.$emit('closeModalOrder')
      },
      getDetailOrder() {
        var self = this
        if (self.orderInfor) {
          axios.get('admin/order/get/' + self.orderID, {
            headers: {Authorization: self.$store.state.userToken}
          })
            .then(response => {
              self.orderInfor = response.data
            })
        }
      },
      calcTotalPrice(quantity, price) {
        return (parseFloat(quantity) * parseFloat(price)).toFixed(0)
      }
    }
  }
</script>
<style lang='sass'>

</style>




